import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useState } from 'react';
import {FaCheckSquare,FaShoppingBasket,FaWindowClose, FaShoppingCart} from 'react-icons/fa';
import { Link } from 'react-router-dom';


export const NavBar = ({filterResult,nbOnCart}) =>
{
    return (<nav className="navbar navbar-light px-3">
                <Link className="navbar-brand" to="/"><FaShoppingBasket/> E-commerc React</Link>
                <form className="form-inline d-flex">
                <span className="mx-2 d-flex">{nbOnCart} <Link to="/cart" ><FaShoppingCart/></Link> </span>
                <input className="form-control mr-sm-2" type="search" placeholder="Search"
                onChange={(e) => filterResult(e.target.value)} />
                
                </form>
            </nav>);
}
export const Card = ({produit}) =>
{
    return (<div className="col-sm-4 my-2">
        <div className="card" >
                <img src={process.env.PUBLIC_URL + 'assets/'+produit.categorie+'/'+produit.image} 
                className="card-img-top" 
                alt={"Image de" + produit.nom } />
                <div className="card-body">
                    <h5 className="card-title">{produit.nom}</h5>
                    <p className="card-text">{produit.prix} fcf{produit.unité}.</p>
                      <label for={produit.id} class="btn btn-primary">
                      Launch demo modal
                      </label>
                </div>
            </div>
            {}
    </div>);
}

export const ProductModal = (prop) =>
{
    const {modalInfo,addToCart} = prop;
    const [productNbWould,setProductNbWould] = useState(1);
    
    
    return (<div class="modal fade" id={modalInfo.id}  role="dialog">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">{modalInfo.nom}</h5>
            <label for={modalInfo.id} data-dismiss="modal" aria-label="Close">
            <FaWindowClose/>
            </label>
        </div>
        <div class="modal-body">
          <div className="row">
            <div className="col">
                <img src={process.env.PUBLIC_URL+ 'assets/'+modalInfo.categorie+'/'+modalInfo.image} 
                alt="" />
                <div className="mt-2">
                    <button className="btn bg-primary" 
                    onClick={() => productNbWould > 1 ? setProductNbWould(productNbWould -1) : 1} >-</button>
                    <span className="px-2">{productNbWould}</span>
                    <button className="btn bg-primary" 
                    onClick={() => setProductNbWould(productNbWould +1)}>+</button>
                </div>
            </div>
            <div className="col">
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
                    tempor incididunt ut labore et dolore magna consequat.</p>
                <h2>{modalInfo.prix +"Fcfa"+modalInfo.unité}</h2>
            </div>
          </div>
        </div>
        <div class="modal-footer">

          <label for={modalInfo.id} class="btn btn-secondary"> Fermer </label>

          <button type="button" class="btn btn-primary" 
          onClick={() => addToCart(modalInfo,productNbWould)} >Ajouter au panier</button>
        </div>
      </div>
    </div>
  </div>);
    
}


export const CardsList = ({storedData,filtered,cartProducts,addToCart}) =>
{
    let finalData = null;
    if(filtered != false)
    { finalData = filtered }
    else
    { finalData = storedData }

    return(<div className="row" id="allProductAndModal">
                {
                    finalData.map(produit =>
                        {
                            return <>
                                    <Card produit={produit} />
                                    <input type="checkbox" id={produit.id} />
                                    <ProductModal modalInfo={produit} cartProducts={cartProducts} addToCart={addToCart} />
                                    </>
                        })
                }
            </div>);
}

export const GetInputState = ({isValid,fields,activeBtn}) =>
{
  let status = null;
  fields.map((field,index) =>
  {
    if(isValid == false)
    {
      activeBtn(false)
      // status += <li>Champs {field.name} non remplis</li>
    }
    if(index == 5 && isValid == true)
    {
      activeBtn(true)
      status = <li>Formulaire rempli avec success</li>
    }
  })
  return (<ul>{status}</ul>)
}


export const PaiementPage = () =>
{
  const [formField,setFormField] = useState({isValid: true,fields: []});
  const [stateBtn,setStateBtn] = useState(false)

  const activeBtn = (param) =>
  {
      setStateBtn(param)
  }

  const handleInputChange = (fieldType,event) =>
  {
    const currentformFields = [...formField.fields];
    const newObject = {name: fieldType,value: event.currentTarget.value}
   
    const res = currentformFields.find(({name}) => name === fieldType);
   
    if(newObject.value != '')
    {
      if(res == undefined)
      {
        currentformFields.push(newObject) ;
      }
      else
      {
        res.value = newObject.value
      }
      setFormField({fields : currentformFields,isValid: true});
    }
    else
    {
      setFormField(prevState => {return{...prevState ,isValid: false}});
    }

    
     
  }
    return (<>
            <GetInputState fields={formField.fields} 
                            isValid={formField.isValid}
                            activeBtn={activeBtn} />
            <h1>Proceder au paiement</h1>
            <form className="row">
                <div className="col-6 mb-3">
                <label for="exampleFormControlInput1" className="form-label">Nom</label>
                <input type="text" className="form-control" placeholder="Doe" name="nom"
                onChange={(event) => handleInputChange('nom',event)} />
                </div>
                <div className="col-6 mb-3">
                <label for="exampleFormControlInput1" className="form-label">Prenom</label>
                <input type="text" className="form-control" placeholder="John" name="prenom"
                onChange={(event) => handleInputChange('prenom',event)} />
                </div>
                <div className="col-12 mb-3">
                <label for="exampleFormControlInput1" className="form-label">Email</label>
                <input type="email" className="form-control" placeholder="name@example.com" name="mail"
                onChange={(event) => handleInputChange('mail',event)} />
                </div>
                <div className="col-12 mb-3">
                <label for="exampleFormControlInput1" className="form-label">Adresse</label>
                <input type="email" className="form-control" placeholder="Godomey,Cocotomey" name="adresse"
                onChange={(event) => handleInputChange('adresse',event)} />
                </div>
                <div className="col-6 mb-3">
                <label for="exampleFormControlInput1" className="form-label">Code Postal</label>
                <input type="email" className="form-control" placeholder="06 BP"
                onChange={(event) => handleInputChange('codePostal',event)} />
                </div>
                <div className="col-6 mb-3">
                <label for="exampleFormControlInput1" className="form-label">Ville</label>
                <input type="email" className="form-control" placeholder="Cotonou,..."
                onChange={(event) => handleInputChange('ville',event)} />
                </div>

                <div className="col-6 mb-3 mx-auto text-center">
                <button className={stateBtn == true ? "btn btn-primary " : "btn btn-primary disabled"} >Je valide les informations</button>
                </div>
            </form>
    </>);
}