import { storedData } from "./data";
import { NavBar, CardsList,PaiementPage } from "./components"
import './styles/MyStyle.css';
import { useState } from "react";
import {BrowserRouter,Switch,Route} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';


const SideMenu = ({getTypeOfData,currentCategory}) =>
{
    const menuLinks = ["Fruits","Legumes","Epiceries","Produits Frais","Boisson"];
    return (<div className="col-sm-2">
            <p>Le Menu</p>
            <ul className="list-group list-unstyled" id="produitListes">
                {menuLinks.map((menuLink,index) =>{
                   if(currentCategory == index)
                   {  return <li className="selected" onClick={() =>  getTypeOfData(index) } >{menuLink}</li> }
                   else
                   {  return <li onClick={() =>  getTypeOfData(index) } >{menuLink}</li> }
                })}
            </ul>
            </div>);
}



const GetCartTotalPrice = ({cartProducts}) =>
{
    let total = 0;
    return (<div className="row">
        <div className="py-2 border-top border-top-3 w-100 bg-success bg-opacity-10 rounded-top d-flex justify-content-between">
        <h3>Taxes </h3>
        <p>0 Fcfa</p>
        </div>
        <div className="py-2 border-top border-top-3 w-100 bg-success bg-opacity-10 d-flex justify-content-between">
        <h3>Prix hors taxe</h3>
            {
                cartProducts.map(cartProduct =>
                {
                    {total = total + (cartProduct.prixUnitaire * cartProduct.nombre)}       
                })
            }
        <p>{total} Fcfa</p>
        </div>
        <div className="py-2 border-top border-top-3 w-100 bg-success bg-opacity-10 d-flex justify-content-between">
        <h3 >Total</h3>
        <p>{total} Fcfa</p>
        </div>

        
        <Link  to="/Paiement">
        <button className="mt-2 btn btn-success w-100">Proceder au paiement</button>
        </Link>
        
        
        </div>);
}


const App = () =>
{
    const [typeOfData,setTypeOfData] = useState(0);
    const [filtered,setFilter] = useState(false);
    const [cartProducts,setCartProduct] = useState({prixTotal: 0 ,produits: [] });

    const updateProductQuantity = (id,typeOperation) =>
    { 
        let currentTable = cartProducts.produits.slice();
        let curentQuantity = currentTable.filter(currentTable => currentTable.id == id)[0].nombre;
        typeOperation == true
        ? currentTable.filter(currentTable => currentTable.id == id)[0].nombre = curentQuantity + 1
        : currentTable.filter(currentTable => currentTable.id == id)[0].nombre = (curentQuantity > 1) ? curentQuantity - 1 : 1

        setCartProduct({produits : currentTable})

    }

    const deleteFromCart = (id) =>
    {
        let currentTable = cartProducts.produits.slice();
        currentTable.splice(id,1);
        setCartProduct({produits : currentTable})
    }
    // Pour ajouter des produit au panier 
    const addToCart = (added,nbAdded) =>
    {
        let total = cartProducts.prixTotal;
        const newCartProduct = {id: added.id,nom: added.nom,img: added.image,categorie: added.categorie,
                            nombre: nbAdded,prixUnitaire: added.prix};
        const currentCartProducts = [...cartProducts.produits];

        if(currentCartProducts.filter(currentCartProducts => currentCartProducts.id == added.id).length == 0)
        {
            currentCartProducts.push(newCartProduct)
        }
        else
        {
            currentCartProducts.filter(currentCartProducts => currentCartProducts.id == added.id)[0].nombre = nbAdded;
        }
        
        currentCartProducts.map(cartProduct =>
            {
                {total = total + (cartProduct.prixUnitaire * cartProduct.nombre)}       
            });
        setCartProduct({prixTotal: total,produits: currentCartProducts})
    }

    // Pour voir le panier
    const GetCartProducts = ({cartProducts}) =>
{
    if(cartProducts.length != 0)
    {
        return (<table className="">
            <tr>
            <th>Nom du produit</th>
            <th>Quantité</th>
            <th>Prix unitaire</th>
            <th>Prix total</th>
            <th>Supprimer</th>
            </tr>
            {
                cartProducts.map(cartProduct =>
                    {
                        // setProductQuantity(cartProduct.nombre);
                        return <tr>
                                <td className="">
                                    {cartProduct.nom}
                                    <img width="50px"
                                    src={process.env.PUBLIC_URL + 'assets/'+cartProduct.categorie+'/'+cartProduct.img} />
                                    {/* {cartProduct.img+cartProduct.categorie} */}
                                </td>
                                <td>
                                    <div className="mt-2">
                                        <button className="btn bg-primary" 
                                        onClick={() => updateProductQuantity(cartProduct.id,false)}>-</button>
                                        <span className="px-2">{cartProduct.nombre}</span>
                                        <button className="btn bg-primary" 
                                        onClick={() => updateProductQuantity(cartProduct.id,true)}>+</button>
                                    </div>
                                </td>
                                <td>{cartProduct.prixUnitaire}</td>
                                <td>{(cartProduct.prixUnitaire) * (cartProduct.nombre)}</td>
                                <td>
                                    <button type="button" className="rounded-circle bg bg-danger btn" 
                                    onClick={() => deleteFromCart(cartProduct.id)}>X</button> </td>
                                </tr>
                                
                    })   
            }</table>);
    }
    else
    {
        return (<b>Aucun produit dans le panier</b>);
    }
}


    // Pour faire apparaitre les produits en fonction de la categorie
    const getTypeOfData = (index) =>
    {
        setTypeOfData(index);
    }

    // Pour faire apparaitre les produits selon ce qui est taper dans la barre de recherche
    const filterResult = (inputValue) =>
    {
        if(inputValue!= '')
        {
            const vanillaProductList = storedData.flat();

            let filteredProductList = vanillaProductList.filter(product =>
            {
                const productName = product.nom.toLowerCase();
                const searchValue = inputValue.toLowerCase();

                return productName.indexOf(searchValue) > -1;
            });
            setFilter(filteredProductList);
        }
        else
        {
            setFilter(false);
            setTypeOfData(0);
        }
    }

    const Accueil = () =>
    {
        return (<div className="row">
                    <SideMenu getTypeOfData ={getTypeOfData} currentCategory = {typeOfData} />
                    <div className="col-sm">
                        <CardsList storedData = {storedData[typeOfData]} 
                        filtered = {filtered} cartProducts={cartProducts} addToCart={addToCart} />
                    </div>
                    {/* <ProductModal/> */}
                </div>)
    }
    const CartPage = () =>
    {
        return (<div className="row" id="pannierPart">
                    <div className="col-8" >
                        <h2>Votre panier</h2>
                        <GetCartProducts cartProducts={cartProducts.produits} />
                    </div>
                    <div className="col-4">
                        <h2>Resumé de la commande</h2>
                        <GetCartTotalPrice cartProducts={cartProducts.produits} />
                    </div>
                </div>)
    }
    return (<>
                <BrowserRouter>
                <NavBar filterResult={filterResult} nbOnCart={cartProducts.produits.length} />
                <div className="container mt-4">
                    <Switch>
                        <Route path='/Paiement' render={() => <PaiementPage/>}></Route>
                        <Route path='/cart' render={() => <CartPage/>}></Route>
                        <Route path='/' render={() => <Accueil/>}></Route>
                    </Switch>
                    </div>
                </BrowserRouter>
            </>);
}

export default App;